.talent {
  margin-top: 30px;
  width: 100%;
  height: 300px;
  background-color: #fff;
}

.talent-items {
  margin: 0;
  padding: 0 100px 50px 100px;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}

.talent-items li {
  padding: 0 40px;
}

@media(max-width: 1200px) {
  .talent {
    height: auto;
  }
  .talent-items {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    padding: 0;
    margin: 0;
  }
}



